<template>
  <div class="setting h100">
    <a-layout class="bt h100">
      <slider-menu  :defaultModule="selectRoute"></slider-menu>
      <a-layout-content>
          <div style="margin-right: 16px" @click="openReference">
              <a-tooltip title="配置参考">
                  <span class="lingCls" style="float: right;margin-top:8px;">
                  <img src="/static/imagin/ling.png" height="40" width="40" />
                  </span>
              </a-tooltip>
          </div>
        <router-view />
        <referenceModal :visible="referenceVisible" :dragable="true" @close="close"></referenceModal>
      </a-layout-content>
    </a-layout>
  </div>
</template>
<script>
import referenceModal from "@/components/myModal/referenceModal.vue";
export default {
  name: "setting",
  components: { referenceModal },
  data() {
    return {
      selectRoute: "",
      referenceVisible: false,
    };
  },
  watch: {
    // 监听路由变化
    $route: {
      handler: function() {
        this.getSelectRoute();
      },
      // 深度观察监听
      deep: true
    }
  },
  methods: {
    getSelectRoute() {
      let thirdModule = this.$fun.getThirdRoute(this);
      if (this.$global.displaySliderSecondMenu.includes(thirdModule)) {
        this.selectRoute = this.$fun.getLastRoute(this);
      } else {
        console.log(thirdModule)
        this.selectRoute = thirdModule;
      }
    },
    close() {
      this.referenceVisible = false;
    },
    openReference() {
      this.referenceVisible = !this.referenceVisible;
    },
  },
  created() {
    this.getSelectRoute();
  }
};
</script>
<style>
.setting {
  height: 100%;
}
.setting .has-error .ant-input-affix-wrapper .ant-input, .login .has-error .ant-input-affix-wrapper .ant-input:hover {
    background: #042031;
}
.setting .ant-form-item {
  margin-bottom: 0;
}
.setting .ant-form-item-label > label{
  color: #fff;
}
</style>