<template>
  <div class="referenceModal">
    <custom-modal :title="title" :visible="visible" :dragable="dragable">
      <div class="content">
        <a-tabs type="card" :default-active-key="referenceKey" tab-position="left">
          <a-tab-pane key="1" tab="基本正则">
            <div id="table" style="max-height:600px;overflow-y:auto;">
              <div>
                <table border="1" style="width:100%;border-color:#fff">
                  <colgroup>
                    <col width="10%" />
                    <col width="10%" />
                    <col width="80%" />
                  </colgroup>
                  <tr style="font-weight: bold;word-break: break-all;">
                    <td>正则名称</td>
                    <td>正则类型</td>
                    <td>正则内容</td>
                  </tr>
                  <tr v-for="reg of baseReg" :key="reg.id">
                    <td style="color: #faad14">{{reg.name}}</td>
                    <td style="word-break: break-all;">{{reg.category}}</td>
                    <td style="word-break: break-all;color: #faad14">{{reg.regex}}</td>
                  </tr>
                </table>
              </div>
            </div>
          </a-tab-pane>
          <a-tab-pane key="2" tab="频道字段">
            <my-loading :loadingVisible="loadingVisible">
              <a-tabs type="card" :default-active-key="channelFirst" @change="channelChange">
                <a-tab-pane :key="channel.ename" :tab="channel.cname" v-for="channel of channels">
                  <div id="table" style="max-height:600px;overflow-y:auto;">
                    <div>
                      <table border="1" style="width:100%;border-color:#fff">
                        <colgroup>
                          <col width="50%" />
                          <col width="50%" />
                        </colgroup>
                        <tr style="font-weight: bold;word-break: break-all;">
                          <td>字段名称</td>
                          <td>字段英文名</td>
                        </tr>
                        <tr v-for="field of fields" :key="field.id">
                          <td style="color: #faad14">{{field.cname}}</td>
                          <td style="word-break: break-all;">{{field.ename}}</td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </a-tab-pane>
              </a-tabs>
            </my-loading>
          </a-tab-pane>
          <a-tab-pane key="3" tab="内置函数">
            <div id="table" style="max-height:600px;overflow-y:auto;">
              <div>
                <table border="1" style="width:100%;border-color:#fff">
                  <colgroup>
                    <col width="25%" />
                    <col width="25%" />
                    <col width="25%" />
                    <col width="25%" />
                  </colgroup>
                  <tr style="font-weight: bold;word-break: break-all;">
                    <td>名称和使用</td>
                    <td>输入参数</td>
                    <td>输出参数</td>
                    <td>描述</td>
                  </tr>
                  <tr v-for="fun of funcs" :key="fun.id">
                    <td style="color: #faad14">{{fun.name}}</td>
                    <td style="word-break: break-all;">{{fun.inputParams}}</td>
                    <td style="word-break: break-all;">{{fun.outputParams}}</td>
                    <td style="word-break: break-all;color: #faad14">{{fun.description}}</td>
                  </tr>
                </table>
              </div>
            </div>
          </a-tab-pane>
        </a-tabs>
      </div>
      <template v-slot:header-meta>
        <a-icon type="close" @click="close"></a-icon>
      </template>
      <template v-slot:footer>
        <div class="footerButtons ar">
          <a-button type="primary" @click="close">关闭</a-button>
        </div>
      </template>
    </custom-modal>
  </div>
</template>
<script>
    export default {
        name: "referenceModal",
        props: {
            visible: {
                type: Boolean,
                required: true,
                default: false,
            },
            dragable: {
                type: Boolean,
                default: false,
            },
        },
        data() {
            return {
                title: "配置参考",
                baseReg: [],
                channels: [],
                fields: [],
                funcs:[],
                channelFirst: "",
                referenceKey: "1",
                loadingVisible: false,
            };
        },
        watch: {
            visible: function (newVal) {
                if (newVal) {
                    this.loadingVisible = true;
                    this.getReg();
                    this.getChannel();
                    this.searchBuiltFunc()
                }
            },
        },
        methods: {
            reset() {
                this.channels = [];
                this.baseReg = [];
                this.fields = [];
                this.funcs = [];
                this.channelFirst = "";
                this.referenceKey = "1";
            },
            close() {
                this.reset();
                this.$emit("close");
            },
            // 获取基本正则数据
            getReg() {
                this.$API.getBasicRegexList({}).then((res) => {
                    if (res.code === 1000) {
                        this.baseReg = res.data;
                        console.log(this.baseReg)
                    } else {
                        this.baseReg = [];
                        this.$message.warning("获取基本正则数据失败");
                    }
                });
            },
            // 获取内置函数数据
            searchBuiltFunc() {
                this.$API.searchBuiltFunction({}).then((res) => {
                    if (res.code === 1000) {
                        this.funcs = res.data;
                        console.log(this.funcs)
                    } else {
                        this.func = [];
                        this.$message.warning("获取内置函数数据失败");
                    }
                });
            },
            // 获取频道数据
            getChannel() {
                this.$API.searchAllChannel().then((res) => {
                    if (res.code === 1000) {
                        this.channels = res.data;
                        this.channelFirst = this.channels[0].ename;
                        this.getFieldsByKey(this.channelFirst);
                    } else {
                        this.channels = [];
                        this.$message.warning("获取频道数据失败");
                    }
                });
            },
            channelChange(key) {
                this.loadingVisible = true;
                this.channelFirst = key;
                this.getFieldsByKey(key);
            },
            // 根据key获取相关频道以及相关字段
            getFieldsByKey(key) {
                let id = this.channels.find((list) => list.ename === key).id;
                this.$API.searchAllFieldByChannelId(id).then((res) => {
                    if (res.code === 1000) {
                        this.fields = res.data;
                    } else {
                        this.fields = [];
                        this.$message.warning("获取字段数据失败");
                    }
                    this.loadingVisible = false;
                });
            },
        },
    };
</script>
<style>
  .referenceModal .content {
    max-height: 600px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  .listContent {
    padding: 10px 12px;
  }
  #table td {
    padding: 10px;
  }
</style>